* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  background-color: #d7afaf;
  justify-content: center;
  align-items: center;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  font-weight: bold;
  line-height: 1.5;
  display: inline-block;
}

img {
  display: block;
}

.timer-container {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.timer-decoration {
  flex-direction: column-reverse;
  justify-content: center;
  align-items: center;
  gap: 20px;
  margin: 20px;
  display: flex;
}

.timer {
  background-color: #bca0a0;
  border: 1px solid #8c7084;
  border-radius: 10px;
  justify-content: center;
  align-items: center;
  margin: 40px;
  display: flex;
  box-shadow: -3px -3px 5px #8c7084, 3px 3px 5px #968282;
}

.field {
  text-align: center;
  margin: 20px;
}

.text {
  text-align: center;
  background-color: #bca0a0;
  border: 1px solid #8c7084;
  border-radius: 13px;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
  padding: 20px;
  font-size: large;
  font-weight: bold;
  display: flex;
  box-shadow: -3px -3px 5px #8c7084, 3px 3px 2px #968282;
}

.button-start {
  color: #000;
  border: 2px solid #8c7084;
  border-radius: 20px;
  margin: auto;
  padding: 15px 25px;
}

.button-start:hover {
  background-color: #c9b3b3;
  box-shadow: inset -2px -2px 4px #76546d, inset 1px 1px 3px #af9898;
}

